body {
  margin: 0;
  box-sizing: border-box;
}

.wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
}

.display {
  height: 25%;
  display: flex;
  flex-direction: column;
  padding: 15px;
  background-color: #333;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

.display > div {
  margin: 0;
  text-align: right;
  color: #fff;
}

.total {
  font-size: 1rem;
}

.inputText {
  font-size: 2rem;
}

.btn {
  display: flex;
  justify-content: space-between;
  height: 75%;
  background-color: #eee;
}

.left-btn {
  display: grid;
  grid-template-columns: auto auto auto;
  align-content: center;
  width: calc(75% - 5px);
  grid-gap: 5px;
  padding: 5px 0;
}

.right-btn {
  display: flex;
  flex-direction: column;
  width: calc(25% - 5px);
  padding: 5px 0;
}

.left-btn > button {
  font-size: 2em;
}

.right-btn > button {
  height: calc(100% / 6);
  margin: 2px 0;
}

.btn-number.last {
  grid-column: 1 / 3;
}

@media (max-width: 1024px) {
  .content-wrapper {
    width: 50%;
  }
}

@media (max-width: 690px) {
  .wrapper {
    width: 70%;
  }
}